/* card set */
/* pagination set */
.pagination {
  display: flex;
  justify-content: flex-end; }
  .pagination li:last-child a {
    border-right: 1px solid #198754; }
  .pagination .pagination-item {
    display: block;
    color: #212529;
    border: 1px solid #198754;
    border-right: 0px;
    padding: 6px 12px; }
    .pagination .pagination-item:hover, .pagination .pagination-item.active {
      background-color: #198754;
      color: #ffffff; }
      .pagination .pagination-item:hover i, .pagination .pagination-item.active i {
        color: white; }

/* step set */
.step-group {
  background-color: #AA0601;
  position: relative; }
  .step-group::before {
    content: '';
    width: 100%;
    height: 1px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    background-color: #e3a4a4; }
  .step-group .step-item {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: #AA0601;
    border: 1px solid #FBF2F2;
    position: relative; }
  .step-group .step-item__active {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: #AA0601;
    border: 1px solid #ffffff;
    position: relative; }
    .step-group .step-item__active::before {
      content: "";
      width: 12px;
      height: 12px;
      background-color: #ffffff;
      border-radius: 50%;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto; }
  .step-group .step-item__check {
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: #AA0601;
    border: 1px solid #ffffff;
    position: relative; }
    .step-group .step-item__check::before {
      content: "✔";
      width: 20px;
      height: 20px;
      background-color: #ffffff;
      border-radius: 50%;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      color: #AA0601;
      font-weight: bold;
      text-align: center;
      line-height: 20px; }

